.dice--container{
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  justify-items: center;
  align-items: center;
  gap: 3vw;
  padding-left: 5vw;
  position: relative;
}

.instructions{
  font-weight: 400;
  margin-top: 0;
  text-align: center;
  font-size: calc(0.8vw + 1rem);
  padding: 1vw;
}

.game--body{
  height: 30vh;
  max-width: 80vw;
  border-radius: 5px;
  padding: 2vw;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.gameTitle{
  font-size: calc(4vw + 1rem);
  margin: 0;
  text-align: center;
  padding: 2vw;
}

.mainContent{
  background-color: #F5F5F5;
  border-radius: 5vw;
  padding: 5vw;
}

.reroll--button{
  display: flex;
  align-items: center;
  justify-content: center;
  height: calc(5vh + 20px);
  width: calc(20vw + 40px);
  border: none;
  border-radius: 6px;
  background-color: #000000f5;
  color: white;
  font-size: calc(1.5vw + 1rem);
  cursor: pointer;
  position: absolute;
  bottom: calc(2.5vh - 150px);
  left: 50%;
  transform: translateX(-50%);
}

.reroll--button:focus {
  outline: none;
}

.reroll--button:active {
  box-shadow: inset 5px 5px 10px -3px rgba(98, 98, 98, 0.7);
}